<template lang="">
    <div class="d-flex flex-column contact-form">
        <b-card class="contact-card">
        <header class="m-3">
            <h2>Drop us a message</h2>
        </header>
        <div class="input-row">
            <b-form-input class="input m-2" v-model="first_name" placeholder="Name"></b-form-input>
            <b-form-input class="input m-2" v-model="email" type="email" placeholder="Email"></b-form-input>
        </div>
        <div class="input-row">
            <b-form-input class="input m-2" v-model="phone" type="tel" placeholder="Number"></b-form-input>

            <b-form-select class="input m-2 select" v-model="selected">
                <b-form-select-option :value="null">Interested in</b-form-select-option>
                <b-form-select-option value="a">Mobile App Development</b-form-select-option>
                <b-form-select-option value="b">Custom Software Development</b-form-select-option>
                <b-form-select-option-group label="Business Automation">
                    <b-form-select-option value="c">Sharepoint Site</b-form-select-option>
                    <b-form-select-option value="d">Payslips</b-form-select-option>
                </b-form-select-option-group>
            </b-form-select>
        </div>

        <div class="input-row">
            <b-form-textarea
            id="textarea"
            class="m-2"
            v-model="message"
            placeholder="Message..."
            rows="3"
            max-rows="6"
            ></b-form-textarea>
        </div>

        <div class="d-flex justify-content-center">
            <b-button variant="primary" @click="submitMessage()" class="w-100 m-3">Submit</b-button>
        </div>
        </b-card>
    </div>
</template>
<script>
export default {
    data(){
        return{
            first_name: "",
            email: "",
            phone: "",
            message: "",
            selected: null
        }
    },
    methods:{
        submitMessage(){
            //commit data ? how to recieve ???
            console.log(`${this.first_name}, ${this.email}, ${this.phone}, ${this.message}, ${this.selected}  `)
        }
    }
}
</script>
<style lang="css">
.contact-form{
    background-color: rgba(255, 255, 255, 0.267);
    border-radius: 6px;
}
.input{
    width: 50%;
}
.select{
    border: 1px solid rgb(207, 203, 203);
    border-radius: 5px;
    width: 167px;
    padding: 0.5rem;
    color: rgb(109, 109, 109);
}
.input-row{
    display: flex;
}
@media only screen and (max-width: 450px) {
  .input-row {
    flex-direction: column;
  }
  .input{
    width: 100%;
  }

}
</style>