<template lang="">
    <div class="main-slide">
        <header>
            <h5 class="text-color">{{data[current].title}}</h5>
        </header>
        <main>
            <p>{{data[current].text}}</p>
        </main>
    </div>
</template>
<script>
export default {
    data() {
      return {
        current: 0,
        data: [
            { title: "An African footprint", image: "footprint.png", text: "No matter where you are, we've got you covered" },
            { title: "Customer Centric", image: "footprint.png", text: "Without people , no business may exist" },
            { title: "Trusted Platforms", image: "footprint.png", text: "It only gets into our basket by ticking every box" },
        ]
      }
    },
    watch:{
        current(){
            setTimeout(() => {
                if(this.current == 2){
                    this.current = 0
                }
                else{
                    this.current++
                }
            }, 4000)
        }
    },
    created(){
        setTimeout(() => {
            this.current = 1
        }, 4000)
    }
}
</script>
<style lang="css">
   .main-slide{
    background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5)), url(../assets/people1.jpg);
    background-position: center;
    background-size: cover;
    padding-top: 10%;
    padding-bottom: 10%;
    color: white;
    min-height: 12rem;
   }
   .text-color{
    color: orange;
  }
</style>